










.demo-headline {
  padding: 50px 0 50px;
  text-align: center;
  }
.demo-logo {
  margin: 10px 0;
  font: 600 70px/80px "Helvetica Neue", Helvetica, Arial, sans-serif;
  letter-spacing: -2px;
  }
.demo-logo .logo {
  width: 269px;
  height: 269px;
  margin: 0 auto 26px;
  overflow: hidden;
  text-indent: -9999em;
  background: url(../assets/logo.png) center 0 no-repeat;
  background-size: 269px 269px;
  }

.demo-logo small {
  display: block;
  padding-top: 5px;
  font-size: 22px;
  font-weight: 700;
  color: rgba(52, 73, 94, .3);
  letter-spacing: 0;
}



